@keyframes roll-vertical {
  0% {
    transform: translateY(0); }
  100% {
    transform: translateY(-50%); } }

@keyframes xpand-out {
  0% {
    left: 50%;
    right: 50%; }
  100% {
    left: 0;
    right: 0; } }

@keyframes xpand-in {
  100% {
    left: 50%;
    right: 50%; }
  0% {
    left: 0;
    right: 0; } }

@keyframes fadein-blur {
  0% {
    filter: blur(0px); }
  100% {
    filter: blur(3px); } }

@keyframes fadein-bgcolor {
  0% {
    background-color: rgba(0, 0, 0, 0); }
  100% {
    background-color: rgba(0, 0, 0, 0.5); } }

@keyframes fadein-drawer-bottom {
  0% {
    transform: translateY(100%);
    opacity: 0; }
  6% {
    opacity: 0; }
  7% {
    opacity: 1; }
  44% {
    transform: translateY(-4.63%); }
  59% {
    transform: translateY(1.64%); }
  73% {
    transform: translateY(-0.58%); }
  88% {
    transform: translateY(0.2%); }
  100% {
    transform: translateY(0%); } }

@keyframes fadein-drawer-top {
  0% {
    transform: translateY(-100%);
    opacity: 0;
    z-index: -4;
    height: 0; }
  100% {
    transform: translateY(0); } }

@keyframes red-border {
  45% {
    border-color: #ea277c; }
  55% {
    border-color: #ea277c; } }

@keyframes clip-fade-in {
  0% {
    clip-path: inset(0px 100% 0px 0px); }
  20% {
    clip-path: inset(0px 100% 0px 0px); }
  100% {
    clip-path: inset(0px 0% 0px 0px); } }

